export function unformatPhoneNumber(
  input: string | undefined
): string | undefined {
  if (typeof input !== "string") {
    return input; // Return undefined if the input is not a string
  }

  // Remove non-numeric characters from the input string
  const numericOnly = input.replace(/\D/g, "");

  // Check if the numericOnly string has at least 11 digits (including the country code)
  if (numericOnly.length >= 11) {
    // Extract the country code and remaining digits
    const countryCode = `+${numericOnly.slice(0, 2)}`;
    const remainingDigits = numericOnly.slice(2);

    // Return the unformatted phone number
    return `${countryCode}${remainingDigits}`;
  } else {
    // If the input doesn't have enough digits, return it as is
    return input;
  }
}
