import { useCallback } from "react";
import { useFetch } from "hooks";
import { CV } from "models";
import { useParams } from "react-router-dom";
import * as yaml from "js-yaml";
import NavyGoldCV from "components/navy-gold";

export default function CVPage() {
  const { url } = useParams();

  const { data, loading } = useFetch(
    useCallback(async (): Promise<CV> => {
      // Fetch the YAML file based on the route parameter
      const response = await fetch(`${url}`);
      if (!response.ok) {
        throw new Error("Failed to fetch CV data");
      }
      const yamlText = await response.text();

      // Parse the YAML text into a JavaScript object
      return yaml.load(yamlText) as CV;
    }, [url])
  );

  if (loading) return <p>loading cv...</p>;

  if (!data) return <p>error loading cv</p>;

  return <NavyGoldCV cv={data} />;
}
