import {
  Heading,
  Image,
  Pane,
  ThemeProvider,
  defaultTheme,
  mergeTheme,
  minorScale,
} from "evergreen-ui";
import { unformatPhoneNumber } from "formatters";
import { CV } from "models";
import { useCallback } from "react";
import { Col, Row } from "react-flexbox-grid";
import { Link } from "react-router-dom";
import { styled } from "styled-components";
import ProgressBar from "./progress";

const theme = mergeTheme(defaultTheme, {
  colors: {
    navy: "#303948",
    gold: "#A89975",
  },
  components: {
    Heading: {
      sizes: {
        800: {
          color: "colors.gold",
          fontFamily: "Cormorant SC",
          fontSize: 72,
          fontWeight: "bolder",
        },
        700: {
          color: "white",
          fontFamily: "Cormorant SC",
          fontSize: 22,
          fontWeight: "bold",
          marginBottom: 6,
        },
        400: {
          color: "colors.gold",
          fontSize: 18,
          fontWeight: "normal",
          textTransform: "uppercase",
          letterSpacing: 3,
        },
        300: {
          color: "white",
          fontSize: 14,
          letterSpacing: 1,
          marginBottom: 4,
        },
      },
    },
    Paragraph: {
      sizes: {
        400: {
          color: "white",
        },
      },
    },
  },
});

export default function NavyGoldCV({ cv }: { cv: CV }) {
  const Name = useCallback(
    () => (
      <HighRow center="xs" middle="xs">
        <Col>
          <Heading size={800}>{cv.contactInfo.name}</Heading>
          <Heading size={400} marginTop={minorScale(8)}>
            {cv.contactInfo.profession}
          </Heading>
        </Col>
      </HighRow>
    ),
    [cv.contactInfo.name, cv.contactInfo.profession]
  );

  return (
    <ThemeProvider value={theme}>
      <Header background="colors.navy" />
      <Row center="xs">
        <Col>
          <Container>
            <Row>
              <Col>
                <SideBar background="colors.gold" marginX={minorScale(2)}>
                  <Pane padding={minorScale(7)}>
                    {cv.contactInfo.picture && (
                      <Pane marginBottom={minorScale(5)}>
                        <Image src={cv.contactInfo.picture} width="100%" />
                      </Pane>
                    )}
                    <Pane marginBottom={minorScale(8)}>
                      <Heading size={700}>Personal Info</Heading>
                      <Heading size={300}>{cv.contactInfo.name}</Heading>
                      <Heading size={300}>{cv.contactInfo.birthday}</Heading>
                    </Pane>
                    <Pane marginBottom={minorScale(8)}>
                      <Heading size={700}>Contact</Heading>
                      <Heading size={300}>{cv.contactInfo.address}</Heading>
                      <Link to={"mailto:" + cv.contactInfo.email}>
                        <Heading size={300}>{cv.contactInfo.email}</Heading>
                      </Link>
                      <Link
                        to={"tel:" + unformatPhoneNumber(cv.contactInfo.phone)}
                      >
                        <Heading size={300}>{cv.contactInfo.phone}</Heading>
                      </Link>
                    </Pane>
                    <Pane marginBottom={minorScale(8)}>
                      <Heading size={700} marginBottom={minorScale(2)}>
                        Skills
                      </Heading>
                      <Pane textAlign="left" paddingX={minorScale(2)}>
                        {cv.skills.map((s) => (
                          <Pane marginBottom={minorScale(2)}>
                            <Heading key={s.name} size={300}>
                              {s.name}
                            </Heading>
                            <ProgressBar percent={s.level} />
                          </Pane>
                        ))}
                      </Pane>
                    </Pane>
                  </Pane>
                </SideBar>
              </Col>
              <Col xs>
                <Head>
                  <Name />
                </Head>
                <Body>
                  {new Array(50).fill(0).map(() => (
                    <Lorem />
                  ))}
                </Body>
              </Col>
            </Row>
          </Container>
        </Col>
      </Row>
      <Footer background="colors.navy" />
    </ThemeProvider>
  );
}

const Header = styled(Pane)`
  position: absolute;
  width: 100%;
  height: 200px;
`;

const Container = styled(Pane)`
  position: relative;
  max-width: 1200px;
`;

const Head = styled(Pane)`
  height: 200px;
`;

const Body = styled(Pane)``;

const SideBar = styled(Pane)`
  position: sticky;
  top: 7vh;
  height: 93vh;
  width: 230px;
`;

const Footer = styled(Pane)`
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
`;

const HighRow = styled(Row)`
  height: 100%;
`;

const Lorem = () => (
  <p>
    Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsa, quis
    reprehenderit praesentium magnam assumenda consequuntur suscipit cum sit
    doloribus voluptas modi dolores vero expedita cupiditate aspernatur? Sed
    unde soluta iure.
  </p>
);
